.check-container {
    display: block;
    position: relative;
    padding-left: 35px;
    margin-bottom: 12px;
    cursor: pointer;
    font-size: 22px;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
}


.div-check { 
    border-left:1px solid #ccc; 
    width:15em; 
    max-height: calc(40vh);
    overflow-y: scroll;
    padding:0 0.5em;
}

.check-container input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;
  }
  
.checkmark {
    position: absolute;
    top: 0;
    left: 0;
    height: 25px;
    width: 25px;
    background-color: #eee;
}
  
  
.check-container:hover input ~ .checkmark {
    background-color: #ccc;
}
  
 
.check-container input:checked ~ .checkmark {
    background-color: rebeccapurple;
}
  
  
.checkmark:after {
    content: "";
    position: absolute;
    display: none;
}
  
 
.check-container input:checked ~ .checkmark:after {
    display: block;
}
  

.check-container .checkmark:after {
    left: 9px;
    top: 5px;
    width: 5px;
    height: 10px;
    border: solid white;
    border-width: 0 3px 3px 0;
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg);
}
